import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import Section from "../components/Section"
import Layout from "../components/Layout"
import FadeInDiv from "../components/FadeInDiv"
import ImageFluidForPath from "../components/ImageFluidForPath"

import styles from "../styles/AboutMe.module.scss"
import ImageFluid from "../components/ImageFluidForPath"

const AboutMe = () => {
  const data = useStaticQuery(graphql`
    query summaryQuery {
      dataJson {
        summary
      }
    }
  `)

  return data ? (
    <div className={styles.root}>
      <div className={styles.dividerBottom}>
        <svg
          data-name="Layer 1"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1200 120"
          preserveAspectRatio="none"
        >
          <path
            d="M321.39,56.44c58-10.79,114.16-30.13,172-41.86,82.39-16.72,168.19-17.73,250.45-.39C823.78,31,906.67,72,985.66,92.83c70.05,18.48,146.53,26.09,214.34,3V0H0V27.35A600.21,600.21,0,0,0,321.39,56.44Z"
            className={styles.shapeFill}
          ></path>
        </svg>
      </div>
      <Layout>
        <Section name={"About Me"} animateFrom={"up"} id="start">
          <div className={styles.contentWrapper}>
            <div className={styles.imgWrapper}>
              <FadeInDiv fromDir={"left"}>
                <ImageFluidForPath path={"proPic.jpg"} />
              </FadeInDiv>
            </div>

            <div className={styles.textBlock}>
              <FadeInDiv fromDir={"right"}>
                <p>{data.dataJson.summary}</p>
              </FadeInDiv>
            </div>
          </div>
        </Section>
      </Layout>
    </div>
  ) : (
    <div>Loading...</div>
  )
}

export default AboutMe
